.ContactData {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    box-shadow: 0 2px 3px #ccc;
    border: 1px solid #eee;
    padding: 10px;
    box-sizing: border-box;
    text-align: -webkit-center;
}

@media (min-width: 700px) {
    .ContactData {
        width: 500px;
    }
}