.Loader,
.Loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.Loader {
  margin: 5em auto;
  font-size: 0.3rem;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(207,143,46, 0.2);
  border-right: 1.1em solid rgba(207,143,46, 0.2);
  border-bottom: 1.1em solid rgba(207,143,46, 0.2);
  border-left: 1.1em solid #cf8f2e;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
